<template lang="jade">
#work-layer
  router-link.return-layer-bg(@click.native='$parent.HideHome(false), $parent.SetAnimation(1, 400, 0), $parent.ScrolledPosTop = 0', to='/')
  .work-bg
    .project-head-wrapper(:style='WrapperHeadColor')
    .project-wrapper
      .project-intro
        .row
          .project-intro-title
            h2.title Haagse Hogeschool
        .row
          .project-detail-data
            ul.work-details
              li
                p.work-class role :
                p.work-attribute branding / web design / front end
              li
                p.work-class typeface :
                p.work-attribute Archive / Helvetica Standard
      .project-items
        .row(v-for="img in List")
          .item
            img(v-lazy="img", :key="img", lazy="loading").web-shot
</template>

<script>
export default {
  data() {
    return {
      WrapperHeadColor: 'background-color: #1f3141',
      List: [
        '/dist/works/hhs/hhs-contact.png',
        '/dist/works/hhs/hhs-krediet.png',
        '/dist/works/hhs/hhs-wat.png',
      ]
    }
  }
}
</script>

<style lang="sass">
  h2.title, p.work-class, p.work-attribute
    color: #f5f5f5

</style>



